@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.form__container {
  display: flex;
  flex-flow: row wrap;
  padding: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.settings-block {
  display: flex;
  flex-direction: column;
  border: 1px solid #E1E2E3;
  flex: 1 1 100%;
  padding: 10px;
}

@media all and (min-width: 600px) {
  .settings-block { flex: 1 0; }
}

.required-symbol {
  color: #ff0000;
}

.ip-address__container {
  display: inline-block;
  position: relative;
  margin-bottom: 20px;
  padding-left: 35px;
}

.text__label-container, .required-symbol, .ip-address__container {
  cursor: pointer;
}

.ip-address__container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: #fff;
  border: solid 1px #E1E2E3;
}

.ip-address__container:hover input ~ .checkmark {
  background-color: #e5e5e5;
}

.checkmark::after {
  content: '';
  position: absolute;
  display: none;
}

.ip-address__container input:checked ~ .checkmark:after {
  display: block;
}

.ip-address__container input:checked ~ .checkmark {
  background-color: #83D0F4;
  border: solid 1px #83D0F4;
}

.ip-address__container .checkmark:after {
  top: 6px;
  left: 6px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #fff;
}

.ip-address__custom-container {
  display: flex;
  flex-direction: column;
}

.ip-address__text {
  margin-left: auto;
}

.ip-address__input {
  width: 350px;
  height: 20px;
  padding: 5px;
  margin: 10px;
  background-color: #fff;
  border: 1px solid #DADBE3;
}

.required__symbol {
  color: #ED9A97;
  cursor: default;
}

.ip-address__input:invalid {
  border: red 2px solid;
  padding: 4px; 
}

.ethernet-sets__ip-address {
  display: flex;
  flex-direction: column;
  margin: 10px 0;
}

.ethernet-sets__title {
  font-size: 20px;
  font-weight: 700;
}

.disabled {
  opacity: 0.4;
  pointer-events: none;
}
.input-container__select {
  width: 250px;
  height: 32px;
  padding: 5px;
  margin: 0 10px;
}

.loading {
  animation:spin 1s linear infinite;
  -webkit-animation:spin 1s linear infinite;
}

@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.error {
  background-color: #ff110063;
}

.error__message {
  top: 40px;
  right: 0;
  position: absolute;
  font-size: 12px;
  color: #ff0000;
}

.wireless-sets {
  display: flex;
  flex-direction: column;
}

.wireless-sets__title {
  font-size: 20px;
  font-weight: 700;
}

.wireless-sets__title {
  font-weight: 700;
  font-size: 20px;
}

.wireless-sets__checkbox, .wireless-sets__sequrity-checkbox {
  position: relative;
  display: inline-block;
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 35px;
}

.wireless-sets__checkbox .wireless-sets__input {
  position: absolute;
  height: 0;
  width: 0;
}

.checkmark__checkbox {
  position: absolute;
  top: -3px;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #fff;
  border: solid 1px #E1E2E3;
}

.checkmark__checkbox::after {
  content: '';
  position: absolute;
  display: none;
}

.wireless-sets__checkbox:hover .wireless-sets__input ~ .checkmark__checkbox {
  background-color: #e5e5e5;
}

.wireless-sets__checkbox .wireless-sets__input:checked ~ .checkmark__checkbox {
  background-color: #83D0F4;
  border: solid 1px #83D0F4;
}

.wireless-sets__checkbox .wireless-sets__input:checked ~ .checkmark__checkbox::after {
  display: block;
}

.wireless-sets__checkbox .checkmark__checkbox::after {
  left: 7px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid #fff;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.refresh-icon {
  border: #2CAFED solid 1px;
  border-radius: 50%;
  align-self: center;
}

.wireless-sets__network-name, .wireless-sets__sequrity-key {
  display: inline-flex;
  justify-content: flex-end;
  position: relative;
}

.network-name__refresk-btn {
  background-color: #fff;
  border: 0;
  cursor: pointer;
  align-self: center;
  width: 27px;
  height: 27px;
  border-radius: 50%;
  padding: 0;
  outline: none;
  margin: 0 40px;
}

.key-container__input {
  width: 350px;
  height: 20px;
  padding: 5px;
  margin: 10px;
  background-color: #fff;
  border: 1px solid #DADBE3;
}

.key-container__input:invalid {
  border: red 2px solid;
  padding: 4px; 
}

.control-btns__save-btn, .control-btns__cansel-btn {
  border: 0;
  outline: 0;
  cursor: pointer;
  padding: 0;
  margin: 0 15px;
  height: 30px;
  width: 150px;
  border-radius: 20px;
  font-size: 16px;
}

.control-btns__save-btn {
  background-color: #1AA8EB;
}

.form__container__control-btns {
  margin: 15px 20px;
}

.control-btns__save-btn {
  color: #fff;
}

.control-btns__cansel-btn {
  background-color: transparent;
  border: 2px solid #1AA8EB;
  color: #1AA8EB;
}

.control-btns__save-btn:active {
  box-shadow: 0px 0px 6px 0px #1AA8EB;
}

.control-btns__cansel-btn:active {
  box-shadow: 0px 0px 6px 0px #1AA8EB;
}


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a {
	color: inherit;
	text-decoration: inherit;
}
